<template>
  <div>
    <div id="md" v-html="markdown"></div>
  </div>
</template>
<script>
import { marked } from "marked";
import axios from "axios";
export default {
  name: "App",
  props: ["file"],
  data() {
    return {
      markdown: "",
    };
  },
  watch: {
    file() {
      this.getSingleFile().then((maxId) => {
        for (let i = 0; i < maxId; i++) {
          const el = document.getElementById(`${i}`);
          el.addEventListener("click", this.addTag);
        }
      });
    },
  },
  methods: {
    transformText(text) {
      const link_pat = /\[\[.*?\]\]/g;
      let id = 0;
      for (let link of text.matchAll(link_pat)) {
        text = text.replace(
          link[0],
          `<button class="link" id=${id}>${link[0].slice(
            2,
            link[0].length - 2
          )}</button>`
        );
        id++;
      }
      return { transformed: text, maxId: id };
    },
    addTag(e) {
      this.$root.$emit("tagAdded", e.target.innerHTML);
    },
    getSingleFile() {
      return new Promise((resolve) => {
        axios.get(`http://127.0.0.1:5000/${this.file}`).then((response) => {
          const data = this.transformText(marked(response.data.file_content));
          this.markdown = data.transformed;
          resolve(data.maxId);
        });
      });
    },
  },
};
</script>
<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

.link {
  background-color: darkred;
  color: #ffffff;
}

#md {
  width: 40%;
  margin: auto;
  text-align: left;
}
</style>
