<template>
  <div id="app" @mouseup="getSelectedText">
    Filename (title?):
    <h1>{{ chosenFile }}</h1>
    <div>
      <b-button
        variant="info"
        @click="removeTag(tag)"
        v-for="tag in tags"
        :key="tag"
        size="sm"
        pill
        >{{ tag }}</b-button
      >
    </div>
    <Home :file="chosenFile"> </Home>
    <b-button
      style="position: fixed; float: right; bottom: 20px"
      @click="nextFile"
      size="lg"
      variant="success"
      >Next</b-button
    >
  </div>
</template>

<script>
import axios from "axios";
import Home from "@/components/Home.vue";
export default {
  name: "App",
  components: { Home },
  data() {
    return {
      allFiles: [],
      chosenFile: undefined,
      tags: [],
    };
  },
  methods: {
    getSelectedText() {
      if (window.getSelection && window.getSelection().toString() != "") {
        this.$root.$emit("tagAdded", window.getSelection().toString());
        document.getSelection().removeAllRanges();
      }
    },
    removeTag(tag) {
      this.tags = this.tags.filter((e) => e != tag);
    },
    addTag(tag) {
      if (!this.tags.includes(tag)) this.tags.push(tag);
    },
    nextFile() {
      axios
        .post(`http://127.0.0.1:5000/store_tags/${this.chosenFile}`, {
          tags: this.tags,
        })
        .then(() => {
          this.tags = [];
          this.chosenFile =
            this.allFiles[Math.floor(Math.random() * this.allFiles.length)];
        });
    },
    getFiles() {
      axios.get("http://127.0.0.1:5000/list_files").then((response) => {
        this.allFiles = response.data.files;
        this.chosenFile =
          this.allFiles[Math.floor(Math.random() * this.allFiles.length)];
      });
    },
  },
  mounted() {
    this.getFiles();
    this.$root.$on("tagAdded", this.addTag);
  },
};
</script>

